header {
  text-align: center;
}

th, td {
  padding: 15px;
}

select {
  background-color: #b5deff;
  padding: 5px 0;
  font-weight: 600;
}

select option {
  font-size: 11px;
}

.innerControls input {
  float: right;
  font-weight: normal;
}

.innerControls.permalink {
  display: inline-block;
}

@media (prefers-color-scheme: dark) {
  body {
    color: #ddd;
    background-color: #111;
  }

  button, optgroup, select {
    background-color: #002846;
  }

  input, textarea {
    background-color: #222;
  }

  label {
    font-weight: initial;
  }

  a {
    color: #337ab7;
  }

  pre {
    color: #ccc;
    background-color: #050505;
    border-color: #333;
  }

  .ace-github {
    color: #fff;
    background: #000;
  }

  .ace-github .ace_gutter {
    color: #888;
    background: #181818;
  }

  .ace-github .ace_marker-layer .ace_active-line {
    background: #111;
  }

  .ace-github.ace_focus .ace_marker-layer .ace_active-line {
    background: #321;
  }

  div.config-editor-commands {
    background-color: #444;
    border-color: #333;
  }

  button.btn {
    color: #050505;
  }

  canvas {
    background: #bbb;
  }
}

#controls {
  width: 80%;
  max-width: 1200px;
  border: 1px solid #606060;
  flex-direction: column;
  margin: 0 auto 20px;
  display: flex;
  overflow: hidden;
}

.demo-controls-wrapper {
  max-width: 100%;
  flex: auto;
  padding: 5px 5px 0 3px;
}

.config-editor-wrapper {
  height: 256px;
  border-top: 1px solid #ccc;
  flex-direction: column;
  flex: auto;
  display: flex;
}

.config-editor-container {
  width: 100%;
  height: 100%;
  flex: auto;
  position: relative;
}

#config-editor {
  position: absolute;
  inset: 0;
}

.config-editor-commands {
  background-color: #ddd;
  border-top: 1px solid #ccc;
  flex: auto;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  display: flex;
}

.config-editor-commands label {
  margin-bottom: 0;
}

.config-editor-commands button {
  padding: 5px 8px;
  font-size: 14px;
}

.innerControls {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
  padding-left: 5px;
  font-size: 12px;
  display: flex;
}

.videoCentered {
  width: 720px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.center {
  width: 70%;
  min-width: 615px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  overflow: hidden;
}

#toggleButtons button {
  width: 16%;
  text-align: center;
  text-overflow: ellipsis;
  background-color: #b5deff;
  padding: 5px;
  font-size: 10pt;
  font-weight: bolder;
  display: inline-block;
  overflow: hidden;
}

#statusOut, #errorOut {
  height: auto;
  max-height: 70px;
  overflow: auto;
}

#streamURL, #streamSelect {
  width: calc(100% - 4px);
  margin-left: 3px;
}

#streamURL {
  margin-bottom: 10px;
  padding-left: 3px;
}

#streamSelect {
  padding: 5px 0;
}

#StreamPermalink {
  overflow-wrap: break-word;
  overflow: hidden;
}

#StreamPermalink a {
  font-family: monospace;
  font-size: 10px;
}

@media only screen and (min-width: 600px) {
  #controls {
    flex-direction: row;
  }

  .demo-controls-wrapper {
    max-width: 50%;
  }

  .config-editor-wrapper {
    height: auto;
    border-top: 0;
    border-left: 1px solid #ccc;
  }
}

